import { useEffect, useState } from "react";
import './app.css'
import colors from 'flat-palettes'


function App() {
  const [color, setColor] = useState([])

  useEffect(() => {
    const loadApi = () => setColor(colors(5))

  loadApi();

},[])
  document.getElementById("root").addEventListener("click", () => window.location.reload());

  
  

  return (
    <>
    <div className="App">

      <h1>Clique em qualquer lugar para gerar uma nova paleta</h1>
        <div className="container">
          <div className="column" style={{ "backgroundColor": `${color[0]}`}}><strong>#{color[0]}</strong></div>
          <div className="column" style={{ "backgroundColor": `${color[1]}`}}><strong>#{color[1]}</strong></div>
          <div className="column" style={{ "backgroundColor": `${color[2]}`}}><strong>#{color[2]}</strong></div>
          <div className="column" style={{ "backgroundColor": `${color[3]}`}}><strong>#{color[3]}</strong></div>
          <div className="column" style={{ "backgroundColor": `${color[4]}`}}><strong>#{color[4]}</strong></div>

        </div>
      

    </div>
  </>
  );
  }
export default App;
